/* eslint-disable react/display-name */
/**
 * System imports
 ============================================================================ */
import React from "react";

import Card from "../../../card";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
     * Properties
     ============================================================================ */
  // eslint-disable-next-line no-unused-vars
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Return
     ============================================================================ */
  return (
    <div className="bd-masthead">
      <Card
        backgroundHeader="Transparent"
        backgroundBody="Transparent"
        border="0"
        header={false}
      >
        <h1>Apgūsti vietējo valūtu tirgus</h1>

        <p className="lead">
          Ātri izveido un piekļūsti tirgus vidēm, izmantojot vietni
          Local-Currency.com, kas ir pasaulē pirmā vietējās tirdzniecības
          platforma, kurā ir viena klikšķa dienas fonds, biznesa rīku komplekts,
          tūlītēji pārskaitījumi un jaudīga valūtas pārvaldība.
        </p>
      </Card>
    </div>
  );
};
